
.manage-cv-button {
  height: 40px;
  padding-left: 15px;
  padding-right: 15px;
  display: inline-block;
  text-align: center;
  background-color: #eeeeee;
  border-radius: 0;
  border-bottom: 1px solid #d9d9d9;
  border-right: 1px solid #d9d9d9;
}

.manage-cv-button.primary {
  color: white;
  background-color: #019c7c
}

.manage-cv-button.primary:hover {
  color: white;
  background-color: #018666
}

.manage-cv-button.primary:visited {
  color: white;
}

.manage-cv-button.default {
  color: #444;
}

.manage-cv-button.default:hover {
  color: #444;
  background-color: #f7f7f7;
}

.manage-cv-button.default:visited {
  color: #444;
}

.manageCVModalClass {
  display: block;
  position: absolute;
  z-index: 9997;
  top: 80px;
  width: 505px;
  left: 25%;
  background: white;
  border-radius: 3px;
  margin: auto;
}

.cv-item-overlay {
  padding: 100px 20px;
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  opacity: 0;
  background-color: rgba(0, 0, 0, 0);
}

.cv-item-overlay:hover {
  opacity: 1;
  background-color: rgba(0, 0, 0, 0.3);
}

.cv-item-button {
  background: #019c7c;
  width: 100%;
  display: inline-block;
  text-align: center;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  border-radius: 20px;
  padding: 8px 20px;
}

.cv-item-button:hover {
  opacity: 0.9;
}

@media screen and (max-width: 959px) {
  .manageCVModalClass {
    left: auto;
    top: 0;
    width: 100%;
    margin-left: 0;
    border-radius: 0;
  }
}




