.likerModalClass {
  display: block;
  position: absolute;
  z-index: 9997;
  top: 80px;
  width: 50%;
  max-height: 400px;
  padding: 20px;
  left: 25%;
  background: white;
  border-radius: 3px;
  margin: auto;
  overflow: auto;
}

.likerOverlayClass {
  background: rgba(30, 30, 30, 0.9);
  top: 0;
  left: 0;
  z-index: 9997;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  box-sizing: border-box;

}

@media screen and (max-width: 959px) {
  .likerModalClass {
    left: auto;
    top: 0;
    width: 100%;
    width: 100%;
    margin-left: 0;
    padding: 20px;
    border-radius: 0;
  }
}




