.product-detail-content img, .product-detail-content iframe {
  width: 100% !important;
  height: auto !important;
}

.product-detail-container {
  padding: 80px 30%;
  background: white;
  margin: 0;
  width: 100%
}

@media screen and (max-width: 700px) {
  .product-detail-container {
    padding: 50px 12%;
  }
}

.product-detail-header {
  margin: 0 0 15px 0;
  padding: 0 0 10px 0;
  font-size: 16px;
  font-weight: 500;
  line-height: 1;
  color: #444;
  border-bottom: 1px solid #ddd;
  overflow: hidden;
}

.detail-comment {
  margin: 0 0 20px 0;
  padding: 40px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.08);
}

.detail-comment-author {
  color: #444;
}

.detail-comment-content {
  font-size: 16px;
  line-height: 1.5;
}




