.au-first {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/first-bg.jpg");
  background-size: cover;
  background-position: right;
}

.au-policy-left {
  padding: 100px 50px 100px 100px;
}

.au-policy-right {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/pattern1.png") repeat #8a0000;
  padding: 100px;
}

@media screen and (max-width: 600px) {
  .au-first {
    background-position: center;
  }
}

@media screen and (max-width: 850px) {
  .au-first {
    background-position: center;
  }
  .au-policy-left {
    padding: 30px;
  }

  .au-policy-right {
    padding: 50px 30px 30px 30px;
  }
}

.au-odd {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/pattern1.png") repeat #8a0000;
}

.au-center {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/pattern1.png") repeat #f4f4f4;
}

#au-intro {
  background: white;
}

.au-odd .bl-title {
  margin: 0;
  padding-bottom: 0;
}

.au-subject {
  text-align: center;
  padding-bottom: 50px;
  padding-top: 30px;
  font-family: Bebasbold, sans-serif;
  font-size: 1.3em;
  line-height: 20px;
}

.au-subject-avatar {
  width: 150px;
  margin-bottom: 15px;
}

.white {
  background-color: white;
}



