.left-panel-wrapper {
  width: 270px;
  padding: 10px;
}

.left-panel {
  background-color: white;
  width: 100%;
  padding-bottom: 20px;
  text-align: center;
  -webkit-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
}

.left-panel h5 {
  font-weight: 600;
}

.hi {
  color: white;
  width: 70px;
  height: 70px;
  -webkit-border-radius: 40px;
  -moz-border-radius: 40px;
  border-radius: 40px;
  background-color: #c50000;
  text-align: center;
  line-height: 70px;
  font-size: 30px;
  font-weight: 600;
  display: inline-block;
}

.hi-wrapper {
  text-align: center;
  padding: 30px 0 20px 0;
}

.left-panel .btn:hover {
  opacity: 0.7
}

.left-panel .btn {
  color: white;
  padding: 4px 10px;
  font-size: 12px;
  margin: 20px 5px;
}

.left-panel .sign-in {
  background-color: #c50000;
}

.left-panel .sign-up {
  background-color: #888888;
}

.left-panel:hover, .left-panel-lower:hover {
  transition: all 0.3s;
  -webkit-box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
  -moz-box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
  box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
}

.left-panel-lower {
  margin: 20px 0;
  background-color: white;
  width: 100%;
  padding: 10px 20px 20px 20px;
  -webkit-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
}

.left-panel-lower a:hover {
  color: #c50000 !important;
}

.left-panel-fixed {
  position: fixed;
  width: 250px;
  top: 120px;
  z-index: -1;
}

.left-panel-logged-in-fixed {
  position: fixed;
  width: 250px;
  top: 120px;
  z-index: -1;
}




