#footer {
  border-top: 1px solid #e5e5e5;
  padding: 30px 30px 20px 30px;
  font-size: 13px;
  color: #999;
  height: 650px;
  background-color: white;
  z-index: 9999;
}

@media screen and (min-width: 760px) {
  #footer {
    height: 300px;
  }
}

#footer ul {
  float: left;
}

#footer li a {
  color: #999;
}

#footer li a:hover {
  color: #444;
}

#footer h3, #footer h5 {
  margin-top: 0;
}

#footer .social-button img {
  width: 24px;
  opacity: .37;
  margin-top: 10px;
  margin-right: 10px;
}

#footer .social-button img:hover {
  opacity: 0.6;
}




