.survey-wrapper {
  width: 400px;
  height: 500px;
  z-index: 1000;
  border:1px solid #d9d9d9;
  background: white;
  position: fixed;
  -webkit-box-shadow: -3px -2px 14px 0px rgba(0,0,0,0.1);
  -moz-box-shadow: -3px -2px 14px 0px rgba(0,0,0,0.1);
  box-shadow: -3px -2px 14px 0px rgba(0,0,0,0.1);
  bottom: -800px;
  transition: bottom 1s;
  right: 0;
  display: flex;
  flex-direction: column;
}
.survey-title {
  background: #c50000;
  color: white;
  padding: 15px 10px;
  display: flex;
  justify-content: space-between;
}

.survey-body {
  overflow-y: scroll;
  flex: 1;
  padding: 10px;
}



