}

#quote-carousel .carousel-control {
  background: none;
  color: #CACACA;
  font-size: 2.3em;
  text-shadow: none;
  margin-top: 30px;
}

#quote-carousel .carousel-control.left {
  left: -60px;
}

#quote-carousel .carousel-control.right {
  right: -60px;
}

#quote-carousel .carousel-indicators {
  right: 50%;
  top: auto;
  bottom: 0px;
  margin-right: -19px;
}

#quote-carousel .carousel-indicators li {
  width: 50px;
  height: 50px;
  margin: 5px;
  cursor: pointer;
  border: 4px solid #CCC;
  border-radius: 50px;
  opacity: 0.4;
  overflow: hidden;
  transition: all 0.4s;
}

#quote-carousel .carousel-indicators .active {
  background: #333333;
  width: 128px;
  height: 128px;
  border-radius: 100px;
  opacity: 1;
  overflow: hidden;
}

.au-feedback {
  font-size: 14px;
}

.carousel-inner {
  min-height: 300px;
}

.item blockquote {
  border-left: none;
  margin: 0;
}

.item blockquote p:before {
  content: "\f10d";
  font-family: 'Fontawesome';
  float: left;
  margin-right: 10px;
}

@media screen and (max-width: 559px) {
  #quote-carousel {
    padding: 0 10px 70px 10px !important;
  }
  #quote-carousel .carousel-indicators .active {
    width: 50px;
    height: 50px;
  }
  #quote-carousel .carousel-indicators li {
    width: 30px;
    height: 30px;
  }
}

#quote-carousel .carousel-control.left, #quote-carousel .carousel-control.right {
  display: none;
}



