.EnrollCourseModalClass {
  position: absolute;
  z-index: 9997;
  top: 100px;
  left: 50%;
  width: 920px;
  margin-left: -460px;
  padding: 40px 45px;
  background: #f4f4f4;
  box-sizing: border-box;
  border-radius: 6px;
  margin-bottom: 20px;
}

.EnrollCourseOverlayClass {
  background: rgba(30, 30, 30, 0.9);
  top: 0;
  left: 0;
  z-index: 9997;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  box-sizing: border-box;

}

@media screen and (max-width: 959px) {
  .EnrollCourseModalClass {
    left: auto;
    top: 0;
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    padding: 20px;
    border-radius: 0;
  }
  #btn-close-modal {
    opacity: .34;
    background-image: url("https://s3-ap-southeast-1.amazonaws.com/cmstatic/images/icon-x-black.png");
  }
  #btn-close-modal:hover {
    opacity: .5;
  }
}



