.product-item {
  position: relative;
  border-radius: 3px;
  background-color: white;
  transition: all 0.3s;
  -webkit-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  -moz-box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
  box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.05);
}

.product-item:hover {
  -webkit-box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
  -moz-box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
  box-shadow: 0px 15px 40px -2px rgba(0, 0, 0, 0.32);
}

.filter-duration {

}

.filter-duration a {
  color: #777;
  margin-right: 20px;
}

.colorme-img {
  border-top-right-radius: 3px;
  border-top-left-radius: 3px;
  border-top: 5px solid white;
  border-left: 5px solid white;
  border-right: 5px solid white;
  width: 100%;
  height: 180px;
  margin: 0;
  overflow: hidden;
  cursor: pointer;
}

.colorme-link {
  width: 100%;
  height: 180px;
  top: 0;
  left: 0;
}

.product-info {
  width: 100%;
  height: 150px;
}

.product-list-wrapper {
  width: 100%;
}

.left-panel-wrapper {
  display: none;
}

.product-wrapper {
  position: relative;
  border-radius: 2px;
  width: 270px;
  height: 350px;
  padding: 10px;
  margin-bottom: 20px;
  float: left;
  //margin: 0 8px 30px 8px !important;
  //background-color: white;
}

@media screen and (max-width: 539px) {
  .product-wrapper {
    width: 100%;
    margin-left: 0 !important;
  }
}

.home-page-wrapper, .group-wrapper {
  display: flex;
  margin: 0 auto;
}

@media screen and (min-width: 540px) {
  .home-page-wrapper, .profile-product-list-wrapper, .group-wrapper {
    width: 540px;
  }
  .product-list-wrapper, .group-main {
    width: 540px;
  }
  .left-panel-wrapper {
    display: none;
  }
}

@media screen and (min-width: 810px) {
  .home-page-wrapper, .profile-product-list-wrapper, .group-wrapper {
    width: 810px;
  }
  .product-list-wrapper, .group-main {
    width: 540px;
  }
  .left-panel-wrapper {
    display: block;
  }
}

@media screen and (min-width: 1080px) {
  .product-list-wrapper, .group-main {
    width: 810px;
  }
  .home-page-wrapper, .profile-product-list-wrapper, .group-wrapper {
    width: 1080px;
  }
  .left-panel-wrapper {
    display: block;
  }
}

@media screen and (min-width: 1350px) {
  .product-list-wrapper, .group-main {
    width: 1080px;
  }
  .home-page-wrapper, .profile-product-list-wrapper, .group-wrapper {
    width: 1350px;
  }
  .left-panel-wrapper {
    display: block;
  }
}

.colorme-click {
  background-color: transparent;
  width: 100%;
  height: 100%;
  z-index: 10000;
}

.colorme-img:hover {
  //opacity: 0.7;
}

.colorme-over {
  padding: 10px;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  background-color: white;
  width: 200px;
  height: 150px;
  -webkit-transition: opacity 200ms;
  -moz-transition: opacity 200ms;
  -ms-transition: opacity 200ms;
  -o-transition: opacity 200ms;
  transition: opacity 200ms;
}

.colorme-over:hover {
  opacity: 0.96;
  cursor: pointer;
}

.colorme-over strong {
  display: block;
  margin: 0 0 4px 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color: #444;
}

span.comment {
  display: block;
  margin: 0 0 10px 0;
  font-size: 11px;
  line-height: 1.4;
  color: #777;
  overflow: hidden;
  text-overflow: ellipsis;
}

.product-info .timestamp {
  display: block;
  margin-top: 5px;
  font-size: 12px;
  font-weight: normal;
  font-style: normal;
  line-height: 1;
  color: #777;
}

.colorme-author-wrapper {
  max-height: 16px;
  margin: 10px 0 0 0;
  padding: 0 0 0 10px;
  font-size: 13px;
  font-weight: 500;
  line-height: 1.4;
  color: #999;
  vertical-align: middle;
}

.colorme-author {
  margin: 0;
  color: #3a8bbb;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  border: 0;
  background: transparent;
  font-weight: 600;
}

.colorme-author:hover {
  color: #297b98;
  line-height: 1.4;
  text-decoration: none;
}

.colorme-author img {
  float: left;
  width: 16px;
  height: 16px;
  margin: 1px 5px 0 0;
  border-radius: 50%;
}

.product-tool {
}

.product-tool span, .product-tool a {
  color: #aaa;
  line-height: 30px;
  font-size: 11px;
}

.product-tool .glyphicon {
  margin-left: 10px;
  margin-right: 4px;
}

.badge-category {
  text-transform: uppercase;
  background: #ccc;
  font-size: 10px;
  margin-left: 5px;
  padding: 3px 6px;
}

.badge-category:hover {
  cursor: pointer;
  background-color: #c50000;
}

.group-wrapper {
  padding-top: 20px;
}



