.ProductModalClass {
  position: absolute;
  z-index: 9997;
  top: 20px;
  left: 50%;
  width: 920px;
  margin-left: -460px;
  padding: 40px 45px;
  background: #fff;
  box-sizing: border-box;
  border-radius: 3px;
  margin-bottom: 20px;
}

.ProductOverlayClass {
  background: rgba(30, 30, 30, 0.9);
  top: 0;
  left: 0;
  z-index: 9997;
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: scroll;
  box-sizing: border-box;
}

.ReactModal__Overlay {
  overflow: scroll;
}

.ReactModal__Body--open {
  overflow-y: hidden;
}

#btn-close-modal {
  position: fixed;
  top: 0px;
  right: 40px;
  width: 20px;
  height: 20px;
  padding: 20px;
  opacity: .5;
  z-index: 9998;
  border: none;
  color: white;
  font-size: 32px;
}

#btn-close-modal:hover {
  opacity: 0.8;
  cursor: pointer;
}

a.link:hover {
  text-decoration: underline;
}

ul.product-stat > li {
  position: relative;
  width: 100%;
  line-height: 1;
  box-sizing: border-box;
  overflow: hidden;
  list-style: none;
  display: block;
  font-size: 14px;
}

ul.product-stat > li span.glyphicon {
  font-size: 20px;
  display: block;
  height: 20px;
  float: left;
  width: 20px;
  margin-right: 5px;
}

ul.product-stat > li a:hover {
  cursor: pointer;
  color: #444;
}

ul.product-stat > li .stat-action {
  display: block;
  padding-top: 10px;
  padding-bottom: 10px;
  color: #999;
  line-height: 22px;
  box-sizing: border-box;
  float: left;
}

.like-count {
  float: right;
  padding-right: 16px;
  height: 42px;
  color: #999;
  line-height: 42px;
  display: block;
}

a.like-count:hover {
  color: #444;
}

@media screen and (max-width: 959px) {
  .ProductModalClass {
    left: auto;
    top: 0;
    width: 100%;
    max-width: 100%;
    margin-left: 0;
    padding: 20px;
    border-radius: 0;
  }
  #btn-close-modal {
    opacity: .34;
    color: black;
    //background-image: url("");
  }
  #btn-close-modal:hover {
    opacity: .5;
  }
}

.product-conversation {
  width: 100%;
}

.product-desc {
  line-height: 1.5;
  color: #555;
  border-bottom: 1px solid #ddd;
  margin-bottom: 10px;
  padding-bottom: 10px;
  padding-top: 20px;
}

.product-content {
  margin-top: 10px;
  color: #555;
  //border-bottom: 1px solid #ddd;
}

.product-content > p {
  //padding: 0 100px;
}

.product-content p {
  text-align: justify;
}

.product-content iframe {
  width: 130% !important;
  margin-left: -15%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.product-content img, .product-content video, .image-wrapper {
  width: 130% !important;
  height: auto !important;
  margin-left: -15%;
  margin-top: 20px;
  margin-bottom: 20px;
}

.timestamp {
  color: #999;
}

.modal-comment {
  border-bottom: 1px solid #ddd;
  padding-bottom: 15px;
}

a.more-products {
  color: #444;
}

.more-products-container {
  width: 100%;
  overflow: hidden;
  padding-bottom: 20px;
  //border-bottom: 1px solid #ddd;
}

.more-products-item {
  width: 24%;
  float: left;
  margin-right: 1%;
  margin-top: 1%;
  display: block;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  padding-bottom: 18%;
  border-radius: 4px;
  -webkit-box-shadow: 2px 2px 5px -2px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 2px 2px 5px -2px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 2px 5px -2px rgba(0, 0, 0, 0.1);
}

.modal-tag {
  white-space: nowrap;
  color: #444;
  margin-left: 5px
}

.modal-tag:hover {
  color: #444;
  cursor: pointer;
}



