
.sidenav {
  position: fixed;
  left: 0;
  top: 54px;
  margin-left: 0;
  width: 280px;
  background-color: white;
  height: 100%;
  padding: 0 0 54px 0;
  min-height: 500px;
  transition: 0.5s;
  border-right: 1px solid #d9d9d9;
  -webkit-box-shadow: 1px 0px 2px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 1px 0px 2px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 1px 0px 2px 0px rgba(0, 0, 0, 0.1);
}

.topic-content img, .topic-content video, .topic-content iframe {
  width: 100% !important;
  height: auto !important;
}

.group-content {
  position: relative;
  width: 100%;
  height: 100%;
  padding-left: 280px;
}

.content-with-sidenav {
  overflow: hidden;
  margin: auto;
  padding-left: 5px;
  padding-right: 5px;
  width: 275px;
  max-width: 100%;
}

@media screen and (max-width: 778px) {
  .sidenav {
    margin-left: -280px !important;
  }
  .content-with-sidenav {
    width: 550px;
  }
  .group-content {
    padding: 20px 0 0 0;
  }
  .routing-bar-sidenav-wrapper {
    padding-left: 0 !important;
  }
}

@media screen and (min-width: 780px) {
  .content-with-sidenav {
    width: 550px;
  }
}

@media screen and (min-width: 1050px) {
  .content-with-sidenav {
    width: 820px;
  }
}

@media screen and (min-width: 1320px) {
  .content-with-sidenav {
    width: 1090px;
  }
}



