@font-face {
  font-family: Bebasbook;
  src: url('http://d1j8r0kxyu9tj8.cloudfront.net/webs/SVN-Bebas+Neue+Book.ttf');
}

@font-face {
  font-family: Bebasbold;
  src: url('http://d1j8r0kxyu9tj8.cloudfront.net/webs/SVN-Bebas+Neue+Bold.ttf');
}

@font-face {
  font-family: Bebasregular;
  src: url('http://d1j8r0kxyu9tj8.cloudfront.net/webs/SVN-Bebas+Neue+Regular.ttf');
}

.bl-fix-routing {
  position: fixed;
  width: 100%;
  top: 50px;
  z-index: 99;
}

.bl-title-big {
  font-family: Montserrat, sans-serif;
  font-size: 3.2em;
  padding-top: 15px;
  font-weight: 600;
  padding-bottom: 15px;
}

.bl-title {
  font-family: Montserrat, sans-serif;
  font-size: 1.8em;
  padding-top: 15px;
  font-weight: 600;
  padding-bottom: 15px;
}

.bl-sub-title {
  font-family: Montserrat, sans-serif;
  font-size: 1.8em;
}

.book-landing-section {
  color: black;
}

.book-landing-section.first {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/firstimage.jpg") repeat;
}

.book-landing-section.even {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/pattern1.png") repeat #e2a500;
}

.book-landing-section.right-image {
  padding: 100px 0 100px 100px;
}

.book-landing-section.left-image {
  padding: 100px 100px 100px 0;
}

.book-landing-section.odd {
  background: url("http://d1j8r0kxyu9tj8.cloudfront.net/webs/pattern1.png") repeat white;
}

.book-landing-section .btn-upload {
}

.bl-btn-wrapper {
  padding-top: 20px;
}

.bl-btn {
  padding: 10px 20px !important;
}

.line {
  width: 50px;
  height: 5px;
}

.line.red {
  background-color: #c50000;
}

.line.black {
  background-color: black;
}

.bl-image-container img {
  width: 100%;
}

.bl-image-container {
  padding: 0;
}

.left-container {
  padding-right: 80px;
}

.bl-right-container {
  padding-left: 80px;
  padding-right: 80px;
  color: white;
}

ul.dashed {
  list-style-type: none;
  padding: 5px;
}

ul.dashed > li {
  text-indent: -5px;
}

ul.dashed > li:before {
  content: " - ";
  text-indent: -5px;
}

.red-text {
  color: #c50000;
}

.btn-magazine {
  border: 1px solid black;
  color: black !important;
}

.mz-first {
  background-image: linear-gradient(top, #ffec00, #ffec00 30%, transparent 30%, transparent 100%);
  background-image: -webkit-linear-gradient(left, #ffec00, #ffec00 30%, transparent 30%, transparent 100%)
}

.mz-authors-title {
  padding-left: 100px;
}

.dot {
  background-color: black;
  border-radius: 50%;
  height: 35px;
  width: 35px;
  margin: 15px;
  display: inline-block;
}

#bl-name {
  font-size: 100px;
}

#bl-bg-panel {
  background: #fff005;
  height: 700px;
  top: 200px;
  left: 0;
  z-index: -3;
  width: 70%;
  position: absolute;
}

.dot-container {
  position: absolute;
  right: 120px;
  bottom: -130px;
  z-index: -1;
}

#ml-icons {
  padding: 100px;
}

#ml-icons img {
  width: 70px;
  margin-bottom: 20px;
  margin-top: 20px;
}

#ml-more-products {
  padding: 100px;
}

#first-dots {
  position: absolute;
  right: 50px;
  bottom: 0px;
  z-index: -1;
}

#ml-arrow {
  float: left;
  width: 120px;
  margin-left: -120px;
  padding-top: 40px;
}

@media screen and (max-width: 540px) {
  #ml-arrow {
    width: 50px;
    margin-left: -50px;
    padding-top: 42px;
  }
  #first-dots {
    right: 30px;
  }
  #ml-more-products {
    padding: 30px;
  }
  #form-buy-book .left-container img {
    top: 50px !important;
  }
  #form-buy-book .left-container {
    height: 300px;
  }
  #ml-icons {
    padding: 30px;
  }
  #ml-icons img {
    width: 50px;
  }

  .mz-authors-title {
    padding-left: 30px;
  }
  #bl-bg-panel {
    top: 100px;
    height: 620px;
  }
  #ml-section-has {
    position: relative;
    padding-top: 50px;
  }
  #ml-section-has .dot-container {
    right: 80px;
    bottom: -50px;
  }
  #bl-name {
    font-size: 80px;
  }
  .bl-title-big {
    font-size: 2.0em;
  }
  #first-after-nav {
    padding-top: 100px !important;
  }

  .dot {
    height: 10px;
    width: 10px;
    margin: 7px;
  }

  .mz-first .bl-image-container {
    z-index: 9;
  }

  .mz-first {
    z-index: -4;
    background-image: linear-gradient(top, #ffec00, #ffec00 70%, transparent 70%, transparent 100%);
    background-image: -webkit-linear-gradient(left, #ffec00, #ffec00 70%, transparent 70%, transparent 100%)
  }
}

@media screen and (max-width: 840px) {
  #ml-first-img {
    margin-top: 80px;
  }
  .dot {
    height: 20px;
    width: 20px;
    margin: 10px;
  }
}



