#home-page-banner {
  width: 100%;
  padding: 10px 20px 25px 20px;
  text-align: center;
  background: #444;
}

#home-page-banner h1 {
  color: #999;
  font-size: 16px;
  font-weight: normal;
  line-height: 1.3;
}

#home-page-banner h1 strong {
  font-size: 18px;
  margin-bottom: 2px;
  color: white;
}

.banner-action {
  width:300px;
  display: inline-block;
  margin-top:12px;
}

.banner-action .btn {
  margin-right: 5px;
  display: inline-block;
  padding: 10px 15px 11px;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 13px;
  font-weight: 500;
  line-height: 17px;
  vertical-align: middle;
  cursor: pointer;
  border: none;
  border-radius: 4px;
}

.banner-action .btn.learn-more {
  background: #999;
  color: white;
}

.banner-action .btn.learn-more:hover {
  background: #888;
}

.banner-action .btn.register-now {
  background: #c50000;
  color: white;
}

.banner-button {
  margin: 0 5px;
  padding:10px 15px!important;
  font-size: 14px;
  border-radius: 3px;
}

.banner-action .btn.register-now:hover {
  background: #b00000;
}



